<template>
    <div class="search-box" @click="focus">
        <i class="icon-search"></i>
        <input type="text" ref="query" v-model="query" class="box" :placeholder="placeholder" />
        <i @click="clear" v-show="query" class="icon-dismiss"></i>
    </div>
</template>

<script>
import { debounce } from "common/js/util";
export default {
    props: {
        placeholder: {
            type: String,
            default: '搜索歌曲、歌手'
        }
    },
    data() {
        return {
            query: ""
        };
    },
    methods: {
        clear() {
            this.query = "";
        },
        setQuery(query) {
            this.query = query;
        },
        focus() {
            this.$refs.query.focus();
        }
    },
    created() {
        this.$watch(
            "query",
            debounce(newQuery => {
                this.$emit("query", newQuery.replace(/\s*/g,""));
            }, 1000)
        );
    }
};
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
@import '~common/stylus/variable';

.search-box {
    display: flex;
    align-items: center;
    box-sizing: border-box;
    width: 100%;
    padding: 0 6px;
    height: 40px;
    background: $color-highlight-background;
    border-radius: 6px;

    .icon-search {
        font-size: 24px;
        color: $color-background;
    }

    .box {
        flex: 1;
        margin: 0 5px;
        line-height: 18px;
        background: $color-highlight-background;
        color: $color-text;
        font-size: $font-size-medium;
        outline: 0;

        &::placeholder {
            color: $color-text-d;
        }
    }

    .icon-dismiss {
        font-size: 16px;
        color: $color-background;
    }
}
</style>


